import React from "react"
import styled from "styled-components"
import HeroUnit from "./hero-unit"
import Link from "./link"

const StyledHeroUnit = styled(HeroUnit)`
  padding: 0;
  height: 100%;
`

const Picture = styled.figure`
  border: 5px solid rgb(187, 187, 187);
  display: flex;
  background-color: rgb(187, 187, 187);
`

const Caption = styled.figcaption`
  padding-left: 1rem;
  color: white;
`

const Article = styled.article`
  margin: 0;
  padding: 1rem;
`

const Image = styled.img`
  width: 100px;
`

const Profile = ({pictureSrc, name, twitterHandle, role, description}) => (
  <StyledHeroUnit>
    <Picture>
      <Image src={pictureSrc} />
      <Caption>{name}<br /><Link inverse href={`https://instagram.com/${twitterHandle}`}>@{twitterHandle}</Link></Caption>
    </Picture>
    <Article>
      <h2>{role}</h2>
      {description}
    </Article>
  </StyledHeroUnit>
)

export default Profile